body {
    font-family: Arial, sans-serif;
    background-color: #fff;
    margin: 0;
    padding: 0;

}
.logo{
    display: flex;
    justify-content:center;
    margin-top: 20px;
    text-decoration: none;
    color:#272727 ;
}

.big-container{
    display: flex;
    justify-content: center;
    color: rgb(195, 195, 195);
    flex-direction: column;
    
}
.join{
    display: flex;
    justify-content: center;
    color: #757575;
    font-size: 12px;
}
.gmail, .whatsap{
    margin-right: 30px;
}
.enter{
    padding: 14px;
  
   }
.line{
    display: flex;
    justify-content: center;
    color: #c2c8cc;
}
#socialLogin{
    display: flex;
    justify-content: center;
   flex-direction: column;
    width: 40%;
    color: #c2c8cc;
    font-size: 18px;
    align-self: center;

}
.whats {
    display: flex;
    justify-content: center;
    flex-direction: row;
}
.mistake{
    display: flex;
    justify-content: center;
    flex-direction: row;
    color: #c2c8cc;
    font-size: 14px;
    text-decoration: none;
}
.rights{
    display: flex;
    justify-content: center;
    flex-direction: row;
    color: #757575;
    font-size: 10px;
}
form {
    display: flex;
    flex-direction: column;
}
.login{
    background-color:#35c5f0;
    font-size: 20px;
    border-radius: 3px;
    padding: 15px;
    
}
label {
    margin-bottom: 8px;
}

button {
    padding: 10px;
    background-color: #35c5f0;
    color: #fff;
    border: none;
    cursor: pointer;
    margin-bottom: 8px;
}

button:hover {
    background-color: #35c5f0;
}

#socialLogin {
    margin-top: 20px;
}

.combine{
    display: flex;
    justify-content: center;
    
}

.changepassword{
    display: flex;
    flex-direction: row;
    margin-right: 30px;
    text-decoration: none;
}
.liner{
    text-decoration: none;
    color: #424242;
    font-size: 15px;
}
#profile {
    display: none;
    margin-top: 20px;
}

.hidden {
    display: none;
}
